<template>
  <div>
    <div class="search">
      <input type="text" v-model="keyword" class="search-input" placeholder="输入城市名或拼音">
    </div>
    <div v-show="keyword" ref="wrapper" class="search-wrapper">
      <ul>
        <li class="item" v-for="item of list" :key="item.id" @click="handleCityClick(item.name)">
          {{item.name}}
        </li>
        <li class="item" v-show="hasData">没有您要搜索的城市</li>
      </ul>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapMutations} from 'vuex'
export default {
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  props: {
    cities: Object
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wrapper, {click: true, taps: true})
  },
  computed: {
    hasData () {
      return !this.list.length
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach(item => {
            if (item.spell.indexOf(this.keyword) > -1 || item.name.indexOf(this.keyword) > -1) {
              result.push(item)
            }
          })
        }
        this.list = result
      }, 100)
    }
  }
}
</script>

<style lang="scss" scoped>
  .search{
    width: 100%;
    height: 72px;
    background: #00bcd4;
    padding: 10px;
    .search-input {
      width: 100%;
      height: 100%;
      border-radius: 8px;
      padding: 0 10px;
      text-align: center;
    }
  }
  .search-wrapper {
    position: absolute;
    left:0;
    top:160px;
    right:0;
    bottom: 0;
    z-index: 101;
    background: #fff;
    overflow: hidden;
    .item{
      padding-left: 20px;
      line-height: 60px;
      border-bottom:1px solid lightgrey;
    }
  }
</style>



